@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@200;300;500;600&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
.loader {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 377px;
}
.input-bid, .bid-form {
	text-align: center;
}
.bid-btn {
	margin: 3rem 0 1rem;
}
.input-field {
	margin: 0 !important;
}
.bid-list {
	text-align: center;
	margin-top: 1rem;
}
.bid-active {
	color: var(--yellow);
}
.bid-finished {
	color: #3aff59;
}
.bid-time {
	padding: 1.5rem 0
}
.bid-club {
	color: var(--yellow);
	font-size: 1.1rem;
}
.need-auth {
	margin-top: 6rem;
	color: var(--red);
}
.bid__input.invalid {
	border-bottom: 1px solid var(--red);
}
.styles_profile__name__3L24h {
	font-size: 1.1rem;
	padding: 0.5rem 0;
	font-weight: 600;
}

.auth__form, .auth__succes{
	margin: 0.5rem auto 0;
	padding: 0.5rem;
}
.auth__input.invalid {
	border-bottom: 1px solid var(--red);
}
.btn-logout {
	background-color: var(--grey) !important;
}
.btn-login {
	margin-top: 10px;
}
.tabs {
	background: none !important;
	height: 36px !important;
}
.tabs .tab a:focus, .tabs .tab a:focus.active {
	background-color: rgb(0, 0, 0, 0.15) !important;
}
.tabs .tab a:hover, .tabs .tab a.active {
	background-color: rgb(0, 0, 0, 0.15) !important;
}
.tabs .tab {
	line-height: 36px !important;
}
.tabs .tab a {
	color: var(--blue) !important;
}
@namespace url("http://www.w3.org/1999/xhtml");

:root {
	--moredark: #202225;
	--dark: #36393f;
	--white: #f9f6f9;
	--grey: #72757b;
	--grey-light: #b9bbbe;
	--blue: #7289d9;
	--orange: #faa61a;
	--green: #43b581;
	--red: #f04747;
	--yellow: #ffff7b;
	--excel: #F93A21;
	--good: #F3CF52;
	--badly: #b9bbbe;
	--bad: #72757b;
	scrollbar-color: #7289d9 #1E232D !important;
  scrollbar-width: thin !important;
}
* {
	scrollbar-width: thin !important;
}
html {
	color: #f9f6f9 !important;
	color: var(--white) !important;
	background-color: #36393f !important;
	background-color: var(--dark) !important;
	font-family: 'Exo 2', sans-serif !important;
}
button {
	font-family: 'Exo 2', sans-serif !important;
}
table {
	border-collapse: inherit !important;
	position: relative;
}
img {
	width: 100%;
}
td, th {
	padding-top: 5px !important;
	padding-bottom: 5px !important;
}
input {
	height: 2rem !important;
	margin: 0 !important;
}
input[type=text] {
	color: #f9f6f9;
	color: var(--white);
}
hr {
	width: 70%;
	margin: 0.4rem auto;
	height: 1px;
	border: none;
	border-top: 1px solid #626262;
	box-sizing: border-box;
}
select {
	padding: 0 !important;
	height: 1.5rem !important;
	border: none !important;
	color: #f9f6f9;
	color: var(--white);
	background: none !important;
}
option {
	color: #36393f;
	color: var(--dark);
}
select:focus {
	outline: 1px solid rgba(255,255,255,0.2) !important;
}

.row {
	position: relative;
}
.input-row {
	margin-bottom: 10px !important;
}

.table-skill-value {
	width: 5.33% !important;
}
.table-skill-name {
	width: 27% !important;
}

.players-table tr {
	height: 28px; 
	font-weight: 300;
}
.skills-table {
	font-weight: 300;
}
.players-table, .skills-table, .skills {
	font-size: 0.8rem;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.skills td {
	padding: 2px 5px 4px !important;
	line-height: 1.4;
	overflow: hidden;
	white-space: nowrap;
}

table.striped > tbody > tr:nth-child(2n+1) {
	background-color: rgb(65, 67, 72) !important;
}
table.striped > tbody > tr.active-player {
	background-color: #7289d9 !important;
	background-color: var(--blue) !important;
}

.table-container {
	height: 450px;
	overflow-y: scroll;
	overflow-x: hidden;
}
.players-count {
	position: absolute;
	right: 20px;
	top: 485px;
	font-size: 0.8rem;
}
.btn, .btn-large, .btn-small {
	background-color: #7289d9 !important;
	background-color: var(--blue) !important;
}
.btn:hover, .btn-large:hover, .btn-small:hover {
	-webkit-filter: brightness(115%) !important;
	        filter: brightness(115%) !important;
}

.sort-col {
	cursor: pointer;
}

.success-transfer {
	color: #3aff59;
}
.hide {
	display: none;
}
.error-msg {
	font-size: 0.8rem;
	color: #f04747;
	color: var(--red);
	padding: 0.2rem;
}
.sell-price, .sell-msg {
	font-size: 1.2rem;
	text-align: center;
}

.help-msg {
	margin-top: 10rem;
	height: 10rem;
	text-align: center;
	font-size: 1.5rem;
}

@media only screen and (max-width: 1440px) {
	.container {
		width: 85% !important;
	}
}


